import './mcTable';


export class Mc {
    constructor(data) {
        this.data = data;
        this.depth = 0;
        this.height = 0;
        this.width = 0;
        this.points = [];
        this.values = [];
        this.level = undefined;
        this.clipPlane = null;
        this.currentLable = null;

        if (data) {
            this.setData(data);
        }
    };
    setData(data) {
        this.data = data;
        this.depth = data.length;
        this.height = data[0].length;
        this.width = data[0][0].length;

        this.startX = 70;
        this.startY = 0;
        this.endX = 140;
        this.endY = 60;
        this.xDelta = (this.endX - this.startX) / this.width;
        this.yDelta = (this.endY - this.startY) / this.height;

        this.points = [];
        this.values = new Array(this.depth * this.height * this.width);
        // Generate a list of 3D points and values at those points
        let index = 0;
        for (let k = 0; k < this.depth; k++) {
            for (let j = 0; j < this.height; j++) {
                for (let i = 0; i < this.width; i++) {
                    let lng = this.startX + i * this.xDelta;
                    let lat = this.startY + j * this.yDelta;
                    this.points.push(this._getPos(lng, lat, k));
                    let value = data[k][j][i];
                    this.values[index] = value;
                    index++;
                }
            }
        }
    };

    _getPos(lng, lat, z) {
        return new THREE.Vector3(
            // x * this.xDelta - this.startX / 2,
            // this.startY + y * this.yDelta - 30,
            lng - 105,
            lat - 30,
            20 * (this.depth - z) / this.depth
        )
    }

    getGeometry(level) {
        this.level = level;
        // Marching Cubes Algorithm

        // Vertices may occur along edges of cube, when the values at the edge's endpoints
        //   straddle the isolevel value.
        // Actual position along edge weighted according to function values.
        let vlist = new Array(12);

        let geometry = new THREE.Geometry();
        let vertexIndex = 0;
        let wh = this.width * this.height;

        for (let z = 0; z < this.depth - 1; z++) {
            for (let y = 0; y < this.height - 1; y++) {
                for (let x = 0; x < this.width - 1; x++) {
                    // index of base point, and also adjacent points on cube
                    //立方体八个顶点索引
                    let p = x + this.width * y + wh * z,
                        px = p + 1,
                        py = p + this.width,
                        pxy = py + 1,
                        pz = p + wh,
                        pxz = px + wh,
                        pyz = py + wh,
                        pxyz = pxy + wh;

                    // store scalar values corresponding to vertices
                    //立方体八个顶点值
                    let value0 = this.values[p],
                        value1 = this.values[px],
                        value2 = this.values[py],
                        value3 = this.values[pxy],
                        value4 = this.values[pz],
                        value5 = this.values[pxz],
                        value6 = this.values[pyz],
                        value7 = this.values[pxyz];

                    // place a "1" in bit positions corresponding to vertices whose
                    //   isovalue is less than given constant.
                    //比较立方体八个顶点值与level值,小于level则将cubeindex相应位置1
                    let cubeindex = 0;
                    if (value0 < level) cubeindex |= 1;
                    if (value1 < level) cubeindex |= 2;
                    if (value2 < level) cubeindex |= 8;
                    if (value3 < level) cubeindex |= 4;
                    if (value4 < level) cubeindex |= 16;
                    if (value5 < level) cubeindex |= 32;
                    if (value6 < level) cubeindex |= 128;
                    if (value7 < level) cubeindex |= 64;

                    // bits = 12 bit number, indicates which edges are crossed by the isosurface
                    //查表取得交叉点
                    let bits = THREE.edgeTable[cubeindex];

                    // if none are crossed, proceed to next iteration
                    //没有交叉点
                    if (bits === 0) continue;

                    // check which edges are crossed, and estimate the point location
                    //    using a weighted average of scalar values at edge endpoints.
                    // store the vertex in an array for use later.
                    //检查哪边存在交叉,计算二维线性插值点vlist,最多有12个交叉点
                    let mu = 0.5;
                    let points = this.points;
                    // bottom of the cube
                    if (bits & 1) {
                        mu = (level - value0) / (value1 - value0);
                        vlist[0] = points[p].clone().lerp(points[px], mu);
                    }
                    if (bits & 2) {
                        mu = (level - value1) / (value3 - value1);
                        vlist[1] = points[px].clone().lerp(points[pxy], mu);
                    }
                    if (bits & 4) {
                        mu = (level - value2) / (value3 - value2);
                        vlist[2] = points[py].clone().lerp(points[pxy], mu);
                    }
                    if (bits & 8) {
                        mu = (level - value0) / (value2 - value0);
                        vlist[3] = points[p].clone().lerp(points[py], mu);
                    }
                    // top of the cube
                    if (bits & 16) {
                        mu = (level - value4) / (value5 - value4);
                        vlist[4] = points[pz].clone().lerp(points[pxz], mu);
                    }
                    if (bits & 32) {
                        mu = (level - value5) / (value7 - value5);
                        vlist[5] = points[pxz].clone().lerp(points[pxyz], mu);
                    }
                    if (bits & 64) {
                        mu = (level - value6) / (value7 - value6);
                        vlist[6] = points[pyz].clone().lerp(points[pxyz], mu);
                    }
                    if (bits & 128) {
                        mu = (level - value4) / (value6 - value4);
                        vlist[7] = points[pz].clone().lerp(points[pyz], mu);
                    }
                    // vertical lines of the cube
                    if (bits & 256) {
                        mu = (level - value0) / (value4 - value0);
                        vlist[8] = points[p].clone().lerp(points[pz], mu);
                    }
                    if (bits & 512) {
                        mu = (level - value1) / (value5 - value1);
                        vlist[9] = points[px].clone().lerp(points[pxz], mu);
                    }
                    if (bits & 1024) {
                        mu = (level - value3) / (value7 - value3);
                        vlist[10] = points[pxy].clone().lerp(points[pxyz], mu);
                    }
                    if (bits & 2048) {
                        mu = (level - value2) / (value6 - value2);
                        vlist[11] = points[py].clone().lerp(points[pyz], mu);
                    }

                    // construct triangles -- get correct vertices from triTable.
                    let i = 0;
                    cubeindex <<= 4; // multiply by 16... 
                    // "Re-purpose cubeindex into an offset into triTable." 
                    //  since each row really isn't a row.

                    // the while loop should run at most 5 times,
                    //   since the 16th entry in each row is a -1.
                    while (THREE.triTable[cubeindex + i] != -1) {
                        let index1 = THREE.triTable[cubeindex + i];
                        let index2 = THREE.triTable[cubeindex + i + 1];
                        let index3 = THREE.triTable[cubeindex + i + 2];

                        geometry.vertices.push(vlist[index1]);
                        geometry.vertices.push(vlist[index2]);
                        geometry.vertices.push(vlist[index3]);
                        let face = new THREE.Face3(vertexIndex, vertexIndex + 1, vertexIndex + 2);
                        geometry.faces.push(face);

                        geometry.faceVertexUvs[0].push([new THREE.Vector2(0, 0), new THREE.Vector2(0, 1), new THREE.Vector2(1, 1)]);

                        vertexIndex += 3;
                        i += 3;
                    }
                }
            }
            console.log('mc: ' + parseInt(z / this.depth * 100) + '%');
        }

        geometry.mergeVertices();
        geometry.computeFaceNormals();
        geometry.computeVertexNormals();

        return geometry;
    }

    getModel(level) {
        let group = new THREE.Group();
        //边框
        let box = new THREE.Mesh(new THREE.BoxGeometry(70, 60, 20, 1, 1, 1));
        //上移10个多距离（多移一点，避免和地面重合）
        box.translateZ(10.11);
        let boxhelper = new THREE.BoxHelper(box, 0xaaaaaa);
        group.add(boxhelper);

        //label
        let len = this.data.length;
        for (let i = 0; i < len; i++) {
            group.add(this.getMarker(i, false));
        }

        this.currentLable = this.getMarker(0, true);
        this.currentLable.element.textContent = 'click';
        //this.currentLable.element.onclick = null;
        this._darg(this.currentLable.element);
        group.add(this.currentLable);

        //mc
        let geometry = this.getGeometry(level);
        let material = new THREE.MeshPhongMaterial({
            color: 0xaabbcc,
            shininess: 5,
            specular: 0xffffff,
            side: THREE.DoubleSide, //双面
            alphaTest: 0.1,
            // vertexColors: THREE.VertexColors,
            // flatShading: false,
            // wireframe: true,

            // map: getTexture(data),
            // opacity: 1,
            // transparent: true,

        });
        //material.map = new THREE.TextureLoader().load('../image/uv_grid.jpg');
        this.mesh = new THREE.Mesh(geometry, material);
        group.add(this.mesh);

        return group;
    };

    getMarker(index, isLable = false) {
        let div = document.createElement('div');
        div.index = index;
        let _this = this;
        if (isLable) {
            div.className = 'axis-label';
            div.title = '拖动做垂直切片';
        } else {
            div.className = 'axis-point';
            div.title = '第' + index + '层';
            div.onclick = function () {
                _this.clip(index);
                _this._setLabelStyle(index);
            }
        }
        let marker = new THREE.CSS2DObject(div);
        marker.position.copy(this._getPos(this.startX, this.startY, index));
        return marker;
    };

    _setLabelStyle(index) {
        this.currentLable.element.textContent = index;
        this.currentLable.element.style.left = 0;
        this.currentLable.element.style.top = 0;
        this.currentLable.position.copy(this._getPos(this.startX, this.startY, index));
    }

    //取得与拖动label距离最近的Z坐标层索引
    _getNearstPoint(label) {
        function getCenter(el) {
            var pos = el.getBoundingClientRect();
            return {
                x: pos.right, //pos.left + (pos.right - pos.left) / 2,
                y: pos.top + (pos.bottom - pos.top) / 2
            }
        }
        var pos = getCenter(label);
        var points = document.getElementsByClassName('axis-point');
        var dists = [];
        for (let i = 0; i < points.length; i++) {
            var p = getCenter(points[i]);
            var d = Math.sqrt((pos.x - p.x) * (pos.x - p.x) + (pos.y - p.y) * (pos.y - p.y));
            dists.push(d);
        }
        let minDist = Math.min.apply(Math, dists);
        if (minDist > 100) {
            return -1;
        } else {
            return dists.indexOf(minDist);
        }
    }

    _darg(obj) {
        let _this = this;
        //鼠标按下 
        obj.onmousedown = function (ev) {
            //IE直接用event或者window.event得到事件本身,其他浏览器函数要获取到事件本身需要从函数中传入
            var oevent = ev || event;
            var distanceX = oevent.clientX - this.offsetLeft;
            var distanceY = oevent.clientY - this.offsetTop;

            //鼠标移动
            document.onmousemove = function (ev) {
                var oevent = ev || event;
                obj.style.left = oevent.clientX + 28 - distanceX + 'px'; //+28
                obj.style.top = oevent.clientY - distanceY + 'px'; //+2
                let index = _this._getNearstPoint(ev.target);
                if (index >= 0) {
                    obj.textContent = index;
                    if (_this.clipPlane) {
                        var z = _this._getPos(_this.startX, _this.startY, index).z;
                        _this.clipPlane.position.setZ(z);
                    }
                } else {
                    obj.textContent = 'X';
                }
            };
            //鼠标放开
            document.onmouseup = function (ev) {
                let index = _this._getNearstPoint(ev.target);
                console.log(index);
                if (index >= 0) {
                    _this._setLabelStyle(index);
                    _this.clip(index);
                } else {
                    _this._setLabelStyle(index);
                    //_this.removeClip();
                }

                document.onmousemove = null;
                document.onmouseup = null;

            };
        };
    };

    removeClip() {
        this.mesh.material.clippingPlanes[0] = null;
        this.clipPlane = null;
    };

    clip(index) {
        index = +index;
        var z = this._getPos(this.startX, this.startY, index).z;
        if (!this.clipPlane) {
            this.mesh.material.clippingPlanes = [new THREE.Plane(new THREE.Vector3(0, 0, -1), z)];
            //建立切片平面并加纹理
            let geometry = new THREE.PlaneGeometry(70, 60, 1, 1);
            let material = new THREE.MeshBasicMaterial({
                //side: THREE.DoubleSide, //双面
                map: this.getTexture(index),
                // opacity: 0.8,
                transparent: true,
                // depthTest: false,
                alphaTest: 0.1,
            });
            material.needsUpdate = true;
            this.clipPlane = new THREE.Mesh(geometry, material)
            let boxhelper = new THREE.BoxHelper(this.clipPlane, 0xaaaaaa);
            this.clipPlane.add(boxhelper);
            this.clipPlane.translateZ(z);
            // this.clipPlane.translateX(35 + 70);
            // this.clipPlane.translateY(30);
            this.mesh.add(this.clipPlane);
        } else {
            this.mesh.material.clippingPlanes[0].constant = z;
            if (this.clipPlane.material.map) {
                this.clipPlane.material.map.dispose();
            }
            this.clipPlane.material.map = this.getTexture(index);
            this.clipPlane.material.needsUpdate = true;
            this.clipPlane.position.setZ(z);
        }
    };

    getTexture(k) {
        var lut = new THREE.Lut('rainbow', 64);
        lut.setMax(100);
        lut.setMin(0);
        var data = this.data[k];
        var width = data[0].length;
        var height = data.length;
        var size = width * height;
        var imageData = new Uint8Array(size * 4);
        //逐点计算纹理
        for (var i = 0; i < height; i++) {
            for (var j = 0; j < width; j++) {
                var z = data[i][j];
                var c = lut.getColor(z);
                var index = (i * width + j) * 4;
                imageData[index] = c.r * 255;
                imageData[index + 1] = c.g * 255;
                imageData[index + 2] = c.b * 255;
                imageData[index + 3] = z < 0.1 ? 30 : 255;
            }
        }
        let texture = new THREE.DataTexture(imageData, width, height);
        texture.minFilter = THREE.NearestFilter;
        texture.needsUpdate = true;
        return texture;
    }

}