import {
    contourModel
} from './contourMesh';

import {
    cloudCountModel
} from './cloudCount'

import {
    Mc
} from './mc';

export var gui;

export function createGui(cloudModel) {
    var params = {
        topIndex: 1,
    }
    gui = new dat.GUI();
    gui.add(params, 'topIndex', 1, 42).onChange(function (value) {
        console.log("onChange:" + value)
        for (var i in cloudModel.children[0].children) {
            cloudModel.children[0].children[i].visible = i > value;
        }
    });

}

export function creatMap() {
    var loader = new THREE.TextureLoader();
    var material = new THREE.MeshPhongMaterial({
        color: 0x999999,
        specular: 0x555555,
        shininess: 5,
        // wireframe: true,
        normalScale: new THREE.Vector2(2, 2),
        map: loader.load('./image/earth_surface1.jpg'),
        normalMap: loader.load('./image/earth_normal.jpg'),
        specularMap: loader.load('./image/earth_specular.jpg'),
        // side: THREE.DoubleSide,
        depthTest: 1,
    });
    var geometry = new THREE.PlaneGeometry(360, 180, 36, 18);
    var plane = new THREE.Mesh(geometry, material);

    var lngLatgrid = new THREE.GridHelper(360, 36, 0x444444, 0x003300);
    lngLatgrid.position.set(0, 0, 0.1);
    lngLatgrid.rotation.x = Math.PI / 2;
    plane.add(lngLatgrid);
    plane.translateX(-105);
    plane.translateY(-30);

    // var box = new THREE.CubeGeometry(360, 180, 1, 1);
    // var boxMesh = new THREE.Mesh(box);
    // boxMesh.translateZ(50);
    // var boxhelper = new THREE.BoxHelper(boxMesh, 0xaaaaaa);
    // plane.add(boxhelper);
    return plane;
}

export function createContour(url, callback) {
    contourModel(url, callback);
}

export function createCloudCount(url, callback) {
    cloudCountModel(url, callback);
}


export function loadCloudMc(url, level, callback) {
    let loader = new THREE.FileLoader();
    loader.setResponseType('text');
    loader.load(url, function (data) {
        if (callback) {
            data = parse(data);
            let mc = new Mc(data);
            let mesh = mc.getModel(level);
            callback(mesh);
        }
    });
}

function parse(txt) {
    //let head = {};
    let i;
    let count = 43,
        index = 0;;
    let values = txt.split(/[\s+\n+]+/);

    let data = new Array(count)
    for (i = 0; i < count; i++) {
        let oneLevel = parseLevel(values, index);
        data[i] = oneLevel.values;
        index += oneLevel.count;
    }

    return data;
}

function parseLevel(data, index) {
    let h = +data[0], //216
        w = +data[1]; //258
    let values = new Array(h);
    let count = w * h + 2;
    for (let i = 0; i < h; i++) {
        let line = new Array(w);
        for (let j = 0; j < w; j++) {
            line[j] = +data[index + i * h + j + 2];
        }
        values[i] = line;
    }
    return {
        values,
        count
    }
}