import '../lib/lut';

var contour = require('./contour');

function parse(txt) {
    //var head = {};
    var i, j, k;
    var count = 43,
        index = 0;;
    var values = txt.split(/[\s+\n+]+/);

    var data = new Array(count)
    for (i = 0; i < count; i++) {
        var oneLevel = parseLevel(values, index);
        data[i] = oneLevel.values;
        index += oneLevel.count;
    }

    return data;
}

function parseLevel(data, index) {
    var h = +data[0], //216
        w = +data[1]; //258
    var values = new Array(h);
    var count = w * h + 2;
    for (var i = 0; i < h; i++) {
        var line = new Array(w);
        for (var j = 0; j < w; j++) {
            line[j] = +data[index + i * h + j + 2];
        }
        values[i] = line;
    }
    return {
        values,
        count
    }
}


function getLineModel(head, lines) {

    var lut = new THREE.Lut('rainbow', 512);
    lut.setMax(100);
    lut.setMin(0);
    var group = new THREE.Group();
    var i, j;
    for (i in lines) {
        var poly = lines[i];
        var pts = [];
        var c = lut.getColor(poly.level);
        if (c) {
            for (j in poly.points) {
                var pt = poly.points[j];
                pt.x = head.beginLon + pt.x * head.lonSpan;
                pt.y = head.beginLat + pt.y * head.latSpan;
                if (pt.y > 90) pt.y = 90;
                if (pt.y < -90) pt.y = -90;
                //var v = utils.geoToVector(pt.x, pt.y, 508);
                var v = new THREE.Vector3(pt.x, pt.y, poly.level / 5 - 70);
                pts.push(v);
                //geoLine.vertices.push(v);
            }
            var curve = new THREE.CatmullRomCurve3(pts);
            var material = new THREE.LineBasicMaterial({
                color: lut.getColor(poly.level),
                opacity: poly.level == 588 ? 2 : 0.7,
                transparent: true,
            });

            var geoLine = new THREE.Geometry();
            geoLine.vertices = curve.getSpacedPoints(1000);
            var line = new THREE.Line(geoLine, material);
            line.level = poly.level;
            group.add(line);
        }
    }
    return group;
};

function getSurfaceModel1(data) {

    var w = 258 - 1, //145
        h = 216 - 1, //73
        xdelta = 70 / 258, //2.5
        ydelta = 60 / 216, //2.5
        xbegin = 70,
        ybegin = 0;

    //顶点数量
    var verCount = (w + 1) * (h + 1);
    //顶点数组
    var vertices = new Float32Array(verCount * 3);
    //顶点索引（三角形）
    var indices = new((vertices.length / 3) > 65535 ? Uint32Array : Uint16Array)((w - 1) * (h - 1) * 6);
    //UV数组
    var uvs = new Float32Array(verCount * 2);
    //颜色数组
    var colors = new Float32Array(verCount * 4);

    var lut = new THREE.Lut('rainbow', 64);
    lut.setMax(100);
    lut.setMin(0);

    var index = 0;
    //顶点、uv
    for (var iy = 0; iy < h + 1; iy++) {
        for (var ix = 0; ix < w + 1; ix++) {
            var lat = ybegin + iy * ydelta;
            var lng = xbegin + ix * xdelta;
            var z = data[iy][ix];
            if (z === undefined) console.log(z);
            var v = new THREE.Vector3(lng, lat, z / 50 + 10);
            vertices[index * 3 + 0] = v.x;
            vertices[index * 3 + 1] = v.y;
            vertices[index * 3 + 2] = v.z;

            uvs[index * 2] = iy / ydelta;
            uvs[index * 2 + 1] = ix / xdelta;

            var c = lut.getColor(z);
            colors[index * 4 + 0] = c.r;
            colors[index * 4 + 1] = c.g;
            colors[index * 4 + 2] = c.b;
            colors[index * 4 + 3] = 0.5; //h < 0.1 ? 0 : 1;
            index++;
        }
    }


    //  b---c
    //  | \ |
    //  a---d
    //  逆时针转为正面
    index = 0;
    for (iy = 0; iy < h; iy++) {
        for (ix = 0; ix < w; ix++) {
            index = iy * w + ix;
            var offset = index * 6;
            var a = index + iy;
            var b = a + w + 1;
            var c = b + 1;
            var d = a + 1;
            //两个三角形：△adb,△cbd
            indices[offset + 0] = a;
            indices[offset + 1] = d;
            indices[offset + 2] = b;
            indices[offset + 3] = c;
            indices[offset + 4] = b;
            indices[offset + 5] = d;
        }
    }
    var geometry = new THREE.BufferGeometry();
    //顶点
    geometry.addAttribute('position', new THREE.BufferAttribute(vertices, 3));
    //面顶点索引
    geometry.setIndex(new THREE.BufferAttribute(indices, 1));
    //uv映射
    geometry.addAttribute('uv', new THREE.BufferAttribute(uvs, 2));
    //颜色
    geometry.addAttribute('color', new THREE.BufferAttribute(colors, 4));
    //计算法向量
    geometry.computeVertexNormals();
    geometry.verticesNeedUpdate = true;
    geometry.colorsNeedUpdate = true;



    geometry = new THREE.PlaneGeometry(70, 60, w, h);

    var material = new THREE.MeshPhongMaterial({
        // color: 0x7777ff,
        // wireframe: true,
        shininess: 30,
        specular: 0xffffff,
        // vertexColors: THREE.VertexColors,
        side: THREE.DoubleSide, //双面
        // map: getTexture(data),
        // opacity: 0.8,
        transparent: true,
    });
    material.map = getTexture(data);
    //material.map = new THREE.TextureLoader().load('../image/uv_grid.jpg');
    material.map.minFilter = THREE.NearestFilter;
    material.map.needsUpdate = true;
    material.needsUpdate = true;


    var mesh = new THREE.Mesh(geometry, material)
    mesh.translateZ(10);
    return mesh;
}

function getSurfaceModel(data) {
    var w = 258 - 1, //145
        h = 216 - 1; //73
    var group = new THREE.Group();
    for (var i = 0; i < data.length; i++) {
        var geometry = new THREE.PlaneGeometry(70, 60, 1, 1);

        var material = new THREE.MeshBasicMaterial({
            // wireframe: true,
            shininess: 30,
            specular: 0xffffff,
            side: THREE.DoubleSide, //双面
            // map: getTexture(data),
            // opacity: 0.8,
            transparent: true,
        });
        material.map = getTexture(data[i], 42 - i);
        //material.map = new THREE.TextureLoader().load('../image/uv_grid.jpg');
        material.map.minFilter = THREE.NearestFilter;
        material.map.needsUpdate = true;
        material.needsUpdate = true;
        var mesh = new THREE.Mesh(geometry, material)
        mesh.translateZ((50 - i) * 1.2);
        mesh.translateX(35 + 70);
        mesh.translateY(30);
        console.log(i);
        group.add(mesh);
    }
    return group;
}

function getTexture(data, k) {
    var lut = new THREE.Lut('rainbow', 64);
    lut.setMax(100);
    lut.setMin(0);

    var width = data[0].length;
    var height = data.length;
    var size = width * height;
    var imageData = new Uint8Array(size * 4);
    //逐点计算纹理
    for (var i = 0; i < height; i++) {
        for (var j = 0; j < width; j++) {
            var z = data[i][j];
            var c = lut.getColor(z);
            var index = (i * width + j) * 4;
            imageData[index] = c.r * 255;
            imageData[index + 1] = c.g * 255;
            imageData[index + 2] = c.b * 255;
            imageData[index + 3] = z < 0.1 ? 0 : 255;

            //边缘检测
            // if (z > 0.1 && i > 0 && i < height - 1) {
            //     var zz = new Array(8);
            //     zz[0] = data[i - 1][j - 1];
            //     zz[1] = data[i - 1][j];
            //     zz[2] = data[i - 1][j + 1];
            //     zz[3] = data[i][j - 1];
            //     zz[4] = data[i][j + 1];
            //     zz[5] = data[i + 1][j - 1];
            //     zz[6] = data[i + 1][j];
            //     zz[7] = data[i + 1][j + 1];
            //     for (var m in zz) {
            //         if (zz[m] == 0) {
            //             imageData[index] = 0;
            //             imageData[index + 1] = 0;
            //             imageData[index + 2] = 0;
            //             imageData[index + 3] = 200;
            //             break;
            //         }
            //     }
            // }

        }
    }
    return new THREE.DataTexture(imageData, width, height);
}

