import * as utils from './utils';


export class View {

    constructor(id) {
        // this.width = 0;
        // this.height = 0;

        if (id) {
            this.init(id);
        }
    };
    init(id) {
        if (!Detector.webgl) {
            //alert("你的浏览器不支持WebGL，建议使用Chrome浏览器");
            Detector.addGetWebGLMessage()
            return;
        }
        this.radius = 500;
        this.rotateSpeed = 10;
        this.autoRotate = false;
        this._clock = new THREE.Clock();
        //容器
        this.container = document.getElementById(id);
        this.scene = new THREE.Scene();
        this.createCamera();

        this.renderer = new THREE.WebGLRenderer({
            antialias: true,
        });
        this.renderer.setPixelRatio(window.devicePixelRatio);
        this.renderer.localClippingEnabled = true;
        this.container.appendChild(this.renderer.domElement);

        this.labelRenderer = new THREE.CSS2DRenderer();
        this.labelRenderer.domElement.style.position = "absolute";
        this.labelRenderer.domElement.style.top = 0;
        this.labelRenderer.domElement.style.pointerEvents = 'none';
        this.container.appendChild(this.labelRenderer.domElement);

        this.resize();
        window.addEventListener("resize", this.resize.bind(this));

        this.addAxis();
        this.addLight();

        this.controls = new THREE.TrackballControls(this.camera, this.renderer.domElement);
        this.controls.minDistance = 10;
        this.controls.maxDistance = 5000;



        this.stats = new Stats();
        this.container.appendChild(this.stats.domElement);
        return this;
    };

    createCamera() {
        var w = this.container.clientWidth;
        var h = this.container.clientHeight;
        var VIEW_ANGLE = 45;
        this.camera = new THREE.PerspectiveCamera(VIEW_ANGLE, w / h, 0.1, 50000);
        this.camera.position.set(0, -70, 70);
        this.camera.lookAt(new THREE.Vector3(105, 30, 0));
        this.add(this.camera);
        return this;
    };

    //添加光源
    addLight(pos) {
        var pos = new THREE.Vector3(-200, -100, 200);
        // 添加点光源
        var pLight = new THREE.PointLight(0x222222);
        pLight.position.copy(pos);
        this.add(pLight);
        //光晕，表示太阳
        var lensflare = new THREE.Lensflare();
        lensflare.position.copy(pos);
        var flareColor = new THREE.Color(0x666666);
        //flareColor.setHSL(0.2, 0.3, 1.0);
        var loader = new THREE.TextureLoader();
        var textureFlare0 = loader.load('./image/lensflare0.png');
        var textureFlare3 = loader.load('./image/lensflare3.png');
        lensflare.addElement(new THREE.LensflareElement(textureFlare0, 700, 0, flareColor));
        lensflare.addElement(new THREE.LensflareElement(textureFlare3, 60, 0.6));
        lensflare.addElement(new THREE.LensflareElement(textureFlare3, 70, 0.7));
        lensflare.addElement(new THREE.LensflareElement(textureFlare3, 120, 0.9));
        lensflare.addElement(new THREE.LensflareElement(textureFlare3, 70, 1));
        this.sun = lensflare;
        this.add(lensflare);
        // 添加平行光源
        //dLight = new THREE.DirectionalLight(0xffffff);
        var light = new THREE.DirectionalLight(0x888888);
        light.position.set(pos);
        this.add(light);
        this.light = light;
        //添加漫反射
        var aLight = new THREE.AmbientLight(0x88888888);
        this.add(aLight);

        pLight = new THREE.PointLight(0x222222);
        pLight.position.copy(new THREE.Vector3(-200, -100, -200));
        this.add(pLight);

        return this;
    }

    resize() {
        var w = this.container.clientWidth;
        var h = this.container.clientHeight;
        this.renderer.setSize(w, h);
        this.camera.aspect = w / h;
        this.camera.updateProjectionMatrix();
        this.labelRenderer.setSize(w, h);
        return this;
    };

    reset() {
        this.controls.reset();
    }



    add(obj) {
        this.scene.add(obj);
        return this;
    };

    //添加坐标轴
    addAxis() {
        var axis = new THREE.AxesHelper(this.radius);
        this.add(axis);
        return this;
    }

    animate() {
        var pos = this.camera.position;
        // this.light.position.copy(pos);

        this.update();

        if (this.autoRotate) {
            var r = Math.sqrt(pos.x * pos.x + pos.z * pos.z);
            var delta = this._clock.getElapsedTime() * 0.02 * this.rotateSpeed + 109.5;
            pos.set(r * Math.sin(-delta), pos.y, r * Math.cos(delta));
        }
        this.labelRenderer.render(this.scene, this.camera)
        this.controls.update();
        this.stats.update();
        this.renderer.render(this.scene, this.camera);
        requestAnimationFrame(this.animate.bind(this));
    }

    update() {
        // var cloudMat = cloudModel.material;
        // cloudMat.normalScale = new THREE.Vector2(params.normalScale, params.normalScale);
        // cloudMat.color.set(params.color);
        // //cloudMat.specular.set(params.specular);
        // //cloudMat.shininess = params.shininess;
        // cloudMat.opacity = params.opacity;
        // cloudMat.flatShading = params.flatShading;
        // cloudMat.emissive.set(params.emissive);
        //cloudModel.visible = params.cloudVisible && cloudModel.material.normalMap != null;
    }
}